export default {
	data() {
		return {
			radioValue:2,
			weekday:1,
			cycle01:1,
			cycle02:2,
			average01:1,
			average02:1,
			checkboxList:[],
			weekList:['','','','','','',''],
			checkNum:this.$options.propsData.check
		}
	},
	name: 'crontab-week',
	props:['check','day'],
	methods: {
		// 
		radioChange(){
			switch(this.radioValue){
				case '1':
					this.$emit('updata','week','*');
					break;
				case '2':
					this.$emit('updata','week','?');
					break;
				case '3':
					this.$emit('updata','week',this.cycle01 + '-' + this.cycle02);
					break;
				case '4':
					this.$emit('updata','week',this.average01 + '#' + this.average02);
					break;
				case '5':
					this.$emit('updata','week',this.weekday+'L');
					break;
				case '6':
					this.$emit('updata','week',this.checkboxString);
					break;
			}
		},
		// radio
		
		// 
		cycleChange(){
			if(this.radioValue==='3'){
				this.$emit('updata','week',this.cycleTotal);
			}
		},
		// 
		averageChange(){
			if(this.radioValue==='4'){
				this.$emit('updata','week',this.averageTotal);
			}
		},
		// 
		weekdayChange(){
			if(this.radioValue==='5'){
				this.$emit('updata','week',this.weekday+'L');
			}
		},
		// checkbox
		checkboxChange(){
			if(this.radioValue==='6'){
				this.$emit('updata','week',this.checkboxString);
			}
		},
		// day
		dayChange(){
			//weekday?
			if(this.$options.propsData.day === '?' && this.radioValue == '2'){
				this.radioValue = '1';
			}else if(this.$options.propsData.day !== '?' && this.radioValue != '2'){
				this.radioValue = '2';
			}
		}
	},
	watch: {
		"radioValue":"radioChange",
		'cycleTotal':'cycleChange',
		'averageTotal':'averageChange',
		'weekdayCheck':'weekdayChange',
		'checkboxString':'checkboxChange',
		'day':'dayChange'
	},
	computed: {
		// 
		cycleTotal:function(){
			this.cycle01 = this.checkNum(this.cycle01,1,7)
			this.cycle02 = this.checkNum(this.cycle02,1,7)
			return this.cycle01+'-'+this.cycle02;
		},
		// 
		averageTotal:function(){
			this.average01 = this.checkNum(this.average01,1,4)
			this.average02 = this.checkNum(this.average02,1,7)
			return this.average01+'#'+this.average02;
		},
		// 
		weekdayCheck:function(){
			this.weekday = this.checkNum(this.weekday,1,7)
			return this.weekday;
		},
		// checkbox
		checkboxString:function(){
			let str = this.checkboxList.join();
			return str===''?'*':str;
		}
	}
}